import React from 'react'

function QueryTopic({ userData, dataSet, actions }) {
    // console.log("querTopic ",props);
    const handleClick=(e,topic)=>{
        const node=e.target.parentNode;
        for(let i of node.childNodes) i.disabled=true;
        userData.queryTopic=topic;
        switch(userData.querySubject){
            case 'Courses':
                actions.handleCourses();
                break;
            case 'FAQs':
                actions.handleFAQs();
                break;
            case 'Queries':
                actions.handleQueries();
                // eslint-disable-next-line
            default:
        }
    }
    const x=userData.querySubject;
    return dataSet[x].topics.map((ele, ind)=>{
        return <button key={ind} className='d-block startBtn btn my-2' onClick={(e)=>handleClick(e,ele)}>{ele}</button>
    })
}

export default QueryTopic
