import React from 'react';

const MessageParser = ({ children, actions }) => {
    const parse = (message) => {
        console.log(message);
        const checker = children.props.state.checker;
        
        if (message === ".restart") {
            actions.reStart();
        }
        else if(message=='.end') checker='end';
        else if(checker==='end') return;     // conversation already completed!
        else if(message==='.menu'){

        }
        else if (checker === 'name') {
            message = message.trim();
            const rx=/^[a-zA-Z ]{2,30}$/;
            if (rx.test(message)) {
                children.props.state.userData.name = message;
                actions.askUserInterest();
            }
            else actions.answerCorrectly('enter a valid name!');
        }
        else if(checker==='QueryDisplay'){
            message = message.trim();
            if(message && message[message.length-1]==='?'){
                const { querySubject, queryTopic } = children.props.state.userData;
                console.log(children.props.state.dataSet[querySubject].QnA[queryTopic]);
                children.props.state.dataSet[querySubject].QnA[queryTopic].questions.push(message);
                console.log(children.props.state.dataSet[querySubject].QnA[queryTopic]);
                actions.resolveQueries();
            }
            else actions.answerCorrectly('enter a valid query!');
        }
        else{
            actions.answerCorrectly();
        }
    };

    return (
        <div>
            {React.Children.map(children, (child) => {
                return React.cloneElement(child, {
                    parse: parse,
                    actions
                });
            })}
        </div>
    );
};

export default MessageParser;