import { createChatBotMessage } from 'react-chatbot-kit';
import MyDataSet from './MyDataSet';
import BotAvatar from '../components/BotAvatar';
import UserAvatar from '../components/UserAvatar';
import GetStarted from '../components/GetStarted';
import UserInterest from '../components/UserInterest';
import QueryTopic from '../components/QueryTopic';
import CourseItem from '../components/CourseItem';
import FAQitem from '../components/FAQitem';
import UserQuery from '../components/UserQuery';

const botName='CourseExplorer';
const config = {
    botName: botName,
    lang: "no",
  initialMessages: [
    createChatBotMessage(
        `Welcomne to the ${botName}, I’m here to help you with your queries regarding our online course system and provide answers of some most frequently asked questions. 😎`),
        createChatBotMessage(`Note: If anytime you want to: restart type -> ".restart" or goto menu section type -> ".menu"`,
        {
          delay: 500,
          widget: 'GetStarted',
        }
      )
  ],
  state: {
    checker: null,
    userData: {
      name: '',
      querySubject: '',
      queryTopic: ''
    }
    ,
    dataSet: MyDataSet
  },
  customComponents: {
     // Replaces the default bot avatar
     botAvatar: (props) => <BotAvatar {...props} />,
     // Replaces the default user icon
     userAvatar: (props) => <UserAvatar {...props} />,
  },
  widgets: [
    {
      widgetName: 'GetStarted',
      widgetFunc: (props) => {return <GetStarted {...props} />},
      mapStateToProps: []
    },
    {
      widgetName: 'UserInterest',
      widgetFunc: (props) => {return <UserInterest {...props} />},
      mapStateToProps: []
    },
    {
      widgetName: 'QueryTopic',
      widgetFunc: (props) => {return <QueryTopic {...props} />},
      mapStateToProps: [ "userData", "dataSet" ]
    },
    {
      widgetName: 'CourseItem',
      widgetFunc: (props) => {return <CourseItem {...props} />},
      mapStateToProps: [ "userData", "dataSet" ]
    },
    {
      widgetName: 'FAQitem',
      widgetFunc: (props) => {return <FAQitem {...props} />},
      mapStateToProps: [ "userData", "dataSet" ]
    },
    {
      widgetName: 'UserQuery',
      widgetFunc: (props) => {return <UserQuery {...props} />},
      mapStateToProps: [ "userData", "dataSet" ]
    }
  ]

};

export default config;