import React from 'react';

const ActionProvider = ({ createChatBotMessage, setState, children }) => {
    
    const c=children.props.children;
    const botName='courseExplorer'
    // an answer correctly action to make user answer in a valid way to the question
    const answerCorrectly = (txt = 'answer correctly the above asked question!') => {
        const msg = createChatBotMessage(`Please, ${txt} 😵`)
        updateState(msg);
    }

    // start by asking for user name just for conversation purpose
    const askName = () => {
        const msg = createChatBotMessage('Please enter your name to proceed! (just for conversation purpose)')
        updateState(msg, 'name');
    }

    // for reStart
   const reStart = () => {
    const msg = [createChatBotMessage(`Welcomne to the ${botName}, I’m here to help you with your queries regarding our online course system and provide answers of some most frequently asked questions. 😎`),
    createChatBotMessage(`Note: If anytime you want to: restart type -> ".restart" or goto menu section type -> ".menu"`, {
      widget: 'GetStarted'
    })]
    setState(prev=>({
      ...prev,
      messages: msg,
      checker:  null
    }))
  }

    // asking user about kind of query
    const askUserInterest = () => {
        const msg = createChatBotMessage(`HI, ${c.props.state.userData.name}. What would you like to know about?`,{
            widget: 'UserInterest',
            delay: 500
        })
        updateState(msg, 'choice');
    }

    // asking user about the topic of query or title of course he/she wants to know about.
    const askQueryTopic = () => {
        const msg = createChatBotMessage(`${c.props.state.userData.name}, pls select what in ${c.props.state.userData.querySubject}, you would like to know about?`,{
            widget: 'QueryTopic',
            delay: 500
        })
        updateState(msg, 'topic');
    }

    // handleCourses - course title is selected now showing details
    const handleCourses = () => {
        console.log("handle courses here!")
        const msg = createChatBotMessage(`The selected course details are: ↓`,{
            widget: 'CourseItem',
            delay: 500
        })
        updateState(msg, 'courseDisplay');
    }

    // handleFAQs - action to handle the display of FAQs of the selected topic
    const handleFAQs = () => {
        const msg = createChatBotMessage(`The most frequently asked questions of ${c.props.state.userData.queryTopic} are: ↓`,{
            widget: 'FAQitem',
            delay: 500
        })
        updateState(msg, 'FAQdisplay');
    }

    // handleQueries - action to handle the user queries
    const handleQueries = () => {
        const msg = createChatBotMessage(`Pls write down a valid query related to ${c.props.state.userData.queryTopic} below with a ? at last 🤠`)
        updateState(msg, 'QueryDisplay');
    }

    // resolveQueries - action to resolve the user queries
    const resolveQueries = () => {
        const msg = createChatBotMessage(`Let me see, 🤔`,{
            withAvatar: true,
            delay: 1000,
            widget: 'UserQuery'
        })
        updateState(msg, 'QueryResolved');
    }

    // for updating the message state of chatbot which keeps track of all our communication
    const updateState = (message, checker = '') => {
        setState((prev) => ({
        ...prev,
        messages: [...prev.messages, message],
        checker: ((checker !== '') ? checker : prev.checker)
        }))
    }

    return (
        <div>
            {React.Children.map(children, (child) => {
                return React.cloneElement(child, {
                    actions: {
                        askName,
                        askUserInterest,
                        answerCorrectly,
                        askQueryTopic,
                        handleCourses,
                        handleFAQs,
                        handleQueries,
                        resolveQueries,
                        reStart
                    },
                });
            })}
        </div>
    );
};

export default ActionProvider;