import React, { useEffect } from 'react'

function FAQitem({ actions, userData, dataSet }) {
    const { querySubject, queryTopic } = userData;

    const FAQtopic = dataSet[querySubject].QnA[queryTopic];
    useEffect(() => {
        console.log(FAQtopic)
        // eslint-disable-next-line
    }, []);
    const handleClick=(e)=>{
        const x=(e.target.childNodes[1]) ? e.target.childNodes[1] : e.target ;
        // console.log(x);
        if(x.innerText==='▼'){
            x.innerText='▲';
        }
        else x.innerText='▼';
    }
    return FAQtopic.questions.map((ele, ind) => {
        return <div key={`FAQ${ind}`}>
            {/* <p className="d-inline-flex gap-1"> */}
            <p className="FAQques startBtn btn btn-secondary text-dark" data-bs-toggle="collapse" href={`#collapseExampleFAQ${ind}`} aria-expanded="false" aria-controls="collapseExample" onClick={handleClick}>
            {ele}
            <a className="mx-1 text-dark" >
                ▼
            </a>
            </p>

            {/* </p> */}
            <div className="collapse my-2" id={`collapseExampleFAQ${ind}`}  style={{"maxHeight": "12rem", 'overflowY': "scroll"}}>
                <div className="card card-body startBtn">
                    {FAQtopic.answers[ind]}
                </div>
            </div>
        </div>
    });
}
export default FAQitem
