import React, { useEffect, useState } from 'react';

// import * as tf from "@tensorflow/tfjs";
import * as qna from "@tensorflow-models/qna";

function UserQuery({ userData, dataSet, actions }) {
    const { querySubject, queryTopic } = userData;
    const QueryTopic = dataSet[querySubject].QnA[queryTopic];
    const ques = (QueryTopic.questions).pop();
    const para = dataSet[querySubject].Para[queryTopic];
    
    const [answer, setAnswer] = useState();
    const [model, setModel] = useState(null);

    const loadModel = async () => {
        console.log('loading!')
        console.log('Question submitted.',ques,para)
        const loadedModel = await qna.load()
        setModel(loadedModel);
        console.log('Model loaded.')
        answerQuestion();
    }

    // Unfortunately this part is showing some errors. But I will soon correct it.

    const answerQuestion = async () => {
        const passage = para
        const question = ques

        const answers = await model.findAnswers(question, passage)
        setAnswer(answers);
        console.log(answers)

    }

    useEffect(() => { loadModel() }, [])
    return (
        <div >
            <p className="startBtn btn btn-secondary text-dark">{ques}</p>
            {model == null ?
                (<div>
                    <div className="startBtn btn btn-secondary text-dark">Model Loading...</div>
                </div>)
                :
                (
                    <div className="startBtn btn">
                    Answers
                    {answer ? answer.map((ans, ind) => {
                        return <p className="startBtn btn btn-secondary text-dark"><b>Answer {ind + 1} - </b>  {ans}</p>
                    }) : ""}
                    </div>
                )
            }
        </div>
    )
}

export default UserQuery
