import React, { useEffect } from 'react'

function CourseItem({ actions, userData, dataSet }) {
    const { querySubject, queryTopic } = userData;

    const course = dataSet[querySubject].details[queryTopic];
    useEffect(() => {
        console.log(course)
        // eslint-disable-next-line
    }, []);
    return (
        <div>
            <div className="card" style={{ "width": "12rem", "marginLeft": "2rem" }}>
                <img src={course.imgLink} className="card-img-top" alt="courseImg" />
                <div className="card-body">
                    <h5 className="card-title">{course.name}</h5>
                    <p className="">
                        <a className="btn btn-primary" data-bs-toggle="collapse" href="#collapseExample" role="button" aria-expanded="false" aria-controls="collapseExample">
                            Read Description
                        </a>
                    </p>
                    <div className="collapse" id="collapseExample" style={{"maxHeight": "12rem", 'overflowY': "scroll"}}>
                        <div className="card card-body">
                            {course.description}
                        </div>
                    </div>
                    <p className="card-text fs-6 fw-bolder">Duration:  {course.duration} weeks</p>
                    <p className="card-text fw-bolder">Price: ₹ {course.charges}</p>
                    <a href={course.link} className="btn btn-primary">Link</a>
                </div>
            </div>
        </div>
    )
}

export default CourseItem
