import React, { useRef } from 'react'

function UserInterest(props) {
    const ref=useRef(null);
    const handleClick = (msg) => {
        // console.log(props.state.userData.movie.pref);
        const node=ref.current;
        for(let i of node.childNodes[0].childNodes) i.disabled=true;
        node.childNodes[1].disabled=true;
        props.state.userData.querySubject = msg;
        props.actions.askQueryTopic();
        // if(props.state.userData.age>=18) props.actions.showAdult();
        // else props.actions.genreSelection()
    }
    return (
        <div ref={ref}>
            <div className="flex">
                <button className="btn startBtn" onClick={() => handleClick('Courses')}>Courses</button>
                <button className="btn startBtn" onClick={() => handleClick('FAQs')}>FAQs</button>
            </div>
            <button className="btn startBtn singleBtn" onClick={() => handleClick('Queries')}>Queries</button>
        </div>
    )
}

export default UserInterest
